
export const initialState = {
  txs: []
}


export default function userReducer(state = initialState, action) {
  switch (action.type) {
    case "USER.SAVE_TX_HASH":
      const {txHash} = action.payload
      var txs = state.txs
      txs.push(txHash)
      return {
        ...state,
        ...{txs: txs}
      }   
    default:
      return state
  }
}
